import { cva } from 'cva'

export const toggle = cva({
  base: `
  relative inline-flex shrink-0 cursor-pointer rounded-full
  focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:ring-offset-2
  `,
  variants: {
    short: {
      false: `
      h-6 w-11 border-2 border-transparent
      transition-colors duration-200 ease-in-out
      `,
      true: 'group h-5 w-10 items-center justify-center',
    },
    modelValue: {
      false: 'bg-gray-200',
      true: 'bg-indigo-600',
    },
  },
})

export const toggleIcon = cva({
  base: `
  pointer-events-none inline-block h-5 w-5 rounded-full
  border border-gray-200 bg-white shadow ring-0 duration-200 ease-in-out
  `,
  variants: {
    short: {
      false: 'relative transition',
      true: 'absolute left-0 transition-transform',
    },
    modelValue: {
      false: 'translate-x-0',
      true: 'translate-x-5',
    },
  },
})

export const toggleWithoutIcon = cva({
  base: `
  pointer-events-none inline-block h-5 w-5 rounded-full
  bg-white shadow ring-0 duration-200 ease-in-out
  `,
  variants: {
    short: {
      false: 'transition-transform',
      true: 'absolute left-0 border border-gray-200 transition',
    },
    modelValue: {
      false: 'translate-x-0',
      true: 'translate-x-5',
    },
  },
})
