<script setup lang="ts">
import { toRefs } from 'vue'
import { useVModel } from '@vueuse/core'
import {
  Switch,
  SwitchDescription,
  SwitchGroup,
  SwitchLabel,
} from '@headlessui/vue'
import { XMarkIcon } from '@heroicons/vue/16/solid'

import { cn } from '@/shared/lib/style'

import { toggle, toggleIcon, toggleWithoutIcon } from './lib'

export interface Props {
  modelValue?: boolean
  icon?: boolean
  short?: boolean
  rightLabel?: boolean
}

const props = defineProps<Props>()

export type Emits = {
  'update:modelValue': [value: boolean]
}

const emits = defineEmits<Emits>()

export interface Slots {
  default?: () => any
  description?: () => any
}

const $slots = defineSlots<Slots>()

defineOptions({ inheritAttrs: false })

const { short, icon } = toRefs(props)

const vModel = useVModel(props, 'modelValue', emits)
</script>

<template>
  <SwitchGroup
    as="div"
    :class="cn(
      ($slots.default || $slots.description) && 'flex items-center justify-between gap-3',
      rightLabel && 'flex-row-reverse',
    )"
  >
    <div
      v-if="$slots.default || $slots.description"
      class="flex grow flex-col"
    >
      <SwitchLabel
        v-if="$slots.default"
        as="span"
        class="text-sm font-medium leading-6 text-gray-900"
        passive
      >
        <slot />
      </SwitchLabel>

      <SwitchDescription
        v-if="$slots.description"
        as="span"
        class="text-sm text-gray-500"
      >
        <slot name="description" />
      </SwitchDescription>
    </div>

    <Switch
      v-bind="$attrs"
      v-model="vModel"
      :class="toggle({ short, modelValue })"
    >
      <span
        v-if="short"
        aria-hidden="true"
        class="pointer-events-none absolute h-full w-full rounded-md bg-white"
      />

      <span
        v-if="short"
        aria-hidden="true"
        class="
        pointer-events-none absolute mx-auto h-4 w-9 rounded-full
        transition-colors duration-200 ease-in-out
        "
        :class="[modelValue ? 'bg-indigo-600' : 'bg-gray-200']"
      />

      <span
        v-if="!icon"
        aria-hidden="true"
        :class="toggleWithoutIcon({ short, modelValue })"
      />

      <span
        v-if="icon"
        :class="toggleIcon({ short, modelValue })"
      >
        <span
          class="
          absolute inset-0 flex h-full w-full items-center
          justify-center transition-opacity
          "
          :class=" [
            modelValue
              ? 'opacity-0 duration-100 ease-out'
              : 'opacity-100 duration-200 ease-in',
          ]"
          aria-hidden="true"
        >
          <svg class="h-3 w-3 text-gray-400" fill="none" viewBox="0 0 12 12">
            <path
              d="M4 8l2-2m0 0l2-2M6 6L4 4m2 2l2 2"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
          </svg>
        </span>

        <span
          class="
          absolute inset-0 flex h-full w-full items-center
          justify-center transition-opacity
          "
          :class=" [
            modelValue
              ? 'opacity-100 duration-200 ease-in'
              : 'opacity-0 duration-100 ease-out',
          ] "
          aria-hidden="true"
        >
          <XMarkIcon class="h-3 w-3 text-indigo-600" />
        </span>
      </span>
    </Switch>
  </SwitchGroup>
</template>
